import Engine from './utils/engine';
// import Engine from './utils/engine3d';
import tumult from 'tumult';

import { linePath, cubicBezierPath } from './utils/paths';
import { getFidelity, loadFont } from './utils/font';
import GothamFont from '../data/Gotham-Bold.ttf';

const simplex2 = new tumult.Simplex2(new Date().getTime());

const WIDTH = 480;
const HEIGHT = 480;

const HEIGHT_MOD = (HEIGHT * 0.05);

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

let pinch = WIDTH * 0.1;
const total = 5;
let t = 0;

const line = new Array(total).fill(0);

function createLine(l) {
  for (let i = 0; i < total; i++) {
    if (i == 0) {
      l[i] = {
        type: 'm',
        x: 0,
        y: simplex2.gen(i, t)
      };
    } else {

      const last = l[i - 1];
      const curr = {
        x: (WIDTH / total) * i,
        y: simplex2.gen(i, t)
      };

      l[i] = {
        type: 'c',
        x1: last.x + pinch,
        y1: last.y,
        x2: curr.x - pinch,
        y2: curr.y,
        ...curr
      };
    }
  }

  t += 0.01;

  return l;
}

function createDottedLine(l) {
  let path = [
    { x: l[0].x, y: l[0].y }
  ];

  const fidelity = 1;

  for (let i = 1; i < l.length; i++) {
    const last = l[i - 1];
    const curr = l[i];
    const { x, y, x1, y1, x2, y2 } = curr;

    switch (l[i].type) {
      case 'm':
        path.push({ x: l[i].x, y: l[i].y });
        break;
      case 'l':
        path = [
          ...path,
          ...linePath(
            last,
            { x, y },
            getFidelity(last, { x, y }, fidelity)
          )
        ];
        break;
      case 'c':
        path = [
          ...path,
          ...cubicBezierPath(
            last,
            { x: x1, y: y1 },
            { x: x2, y: y2 },
            { x, y },
            getFidelity(last, { x, y }, fidelity)
          )
        ];
        break;
    }
  }

  return path;
}

let mainPath = createDottedLine(createLine(line));

const getDiff = ({ boundingBox: bb1 }, { boundingBox: bb2 }) => {
  if (bb1.width < bb2.width) {
    return bb2.width - bb1.width;
  }

  return 0;
}

function drawWord(path, height, curve, top = true) {
  let invertHeight = top ? 0 : 1;
  let invertY = top ? -1 : 1

  ctx.beginPath();
  for (let s = 0; s < path.length; s++) {
    for (let i = 0; i < path[s].length; i++) {
      let idx = Math.floor(path[s][i].x) - 1;
      let curveIdx = idx;

      for (let x = idx; x < curve.length; x++) {
        if (curve[x].x > path[s][i].x) {
          break;
        }

        curveIdx = x;
      }

      if (i == 0) {
        ctx.moveTo(
          path[s][i].x,
          path[s][i].y + ((invertHeight - (path[s][i].y / height)) * ((curve[curveIdx].y * invertY) * HEIGHT_MOD)),
        );
      } else {
        ctx.lineTo(
          path[s][i].x,
          path[s][i].y + ((invertHeight - (path[s][i].y / height)) * ((curve[curveIdx].y * invertY) * HEIGHT_MOD)),
        );
      }
    }
    ctx.closePath();
  }
  ctx.fill();
}


loadFont(GothamFont)
  .then(Font => {
    let padding = HEIGHT * 0.01;
    let path1 = Font.getOptimisedPath("HEAVY", 80, { fidelity: 1 });
    let path2 = Font.getOptimisedPath("METAL", 80, { fidelity: 1 });

    let flatPath1 = path1.points.map(s => {
      return s.map(p => {
        return {
          x: p.x + getDiff(path1, path2),
          y: p.y + path2.boundingBox.height
        }
      });
    });
    let flatPath2 = path2.points.map(s => {
      return s.map(p => {
        return {
          x: p.x + getDiff(path2, path1),
          y: p.y + path2.boundingBox.height
        }
      });
    });

    let draw = true;
    app.onRender(({ frame, width, height, debug }) => {
      if (draw) {
        mainPath = createDottedLine(createLine(line));
      }

      ctx.fillStyle = '#222';
      ctx.fillRect(0, 0, width, height);

      ctx.save();
      ctx.translate(
        (width - path1.boundingBox.width) / 2,
        height / 2
      );

      ctx.save();
      ctx.translate(0, -(path1.boundingBox.height + padding));
      ctx.fillStyle = '#333';
      drawWord(flatPath1, path1.boundingBox.height, mainPath, true);
      ctx.restore();

      ctx.save();
      ctx.translate(0, padding);
      ctx.fillStyle = '#555';
      drawWord(flatPath2, path2.boundingBox.height, mainPath, false);
      ctx.restore();

      // for (let i = 0; i < mainPath.length; i++) {
      //   ctx.fillRect(mainPath[i].x, mainPath[i].y * HEIGHT_MOD, 1, 1);
      // }
      ctx.restore();
    });

    app.canvasEl.addEventListener('click', e => draw = !draw);

    app.start();
  });
